/************************** 常用类 **************************/
.fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.fill-fixed {
  position: fixed;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.align-center {
  display: flex;
  align-items: center;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.between {
  display: flex;
  justify-content: space-between;
}
.between-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.column {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}
.column-center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.transition {
  transition: all 0.3s;
}
.overflow {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.pointer {
  cursor: pointer;
}
/************************** 常用类 **************************/
/************************** 常用字颜色 **************************/
.primary,
.edit {
  color: #1890ff;
}
.success,
.details {
  color: #52c41a;
}
.warning {
  color: #faad14;
}
.error,
.delete {
  color: #f5222d;
}
.secondary {
  color: rgba(0, 0, 0, 0.45);
}
.disabled {
  color: rgba(0, 0, 0, 0.25);
}
/************************** 常用字颜色 **************************/
/************************** 页面默认布局 **************************/
#root {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  line-height: 1;
  font-size: 16px;
  background-color: #fff;
}
.box {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}
.box > :not(.box-fill) {
  flex-shrink: 0;
}
.box > .box-fill {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  flex: 1;
  position: relative;
}
.box > .box-fill > * {
  flex-shrink: 0;
}
.page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
}
.page > :not(.page-fill) {
  flex-shrink: 0;
}
.page > .page-fill {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  flex: 1;
  position: relative;
}
.page > .page-fill > * {
  flex-shrink: 0;
}
/************************** 页面默认布局 **************************/
